@include font('proximanova-regular', 'proximanova-regular-webfont');
@include font('proximanova-bold', 'proximanova-bold-webfont');
@include font('proximanova-semibold', 'proximanova-semibold-webfont');
@include font('proximanova-light', 'proximanova-light-webfont');
@include font('proximanovat-thin', 'proximanovat-thin-webfont');

@mixin reg {
  font-family: 'proximanova-regular', Arial, sans-serif; }
@mixin bold {
  font-family: 'proximanova-bold', Arial, sans-serif; }
@mixin semi {
  font-family: 'proximanova-semibold', Arial, sans-serif; }
@mixin light {
  font-family: 'proximanova-light', Arial, sans-serif; }
@mixin thin {
  font-family: 'proximanovat-thin', Arial, sans-serif; }


@mixin xl {
  @include r(1399) {
    @content; } }
@mixin lg {
  @include r(1199) {
    @content; } }
@mixin md {
  @include r(991) {
    @content; } }
@mixin sm {
  @include r(767) {
    @content; } }
@mixin xs {
  @include r(575) {
    @content; } }

@mixin lgmin {
  @include r(1200) {
    @content; } }
@mixin mdmin {
  @include r(992) {
    @content; } }
@mixin smmin {
  @include r(768) {
    @content; } }
@mixin xsmin {
  @include r(576) {
    @content; } }


body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  @include reg;
  color: $c-text;
  @include no-scroll;
  background-color: $black; }

.out {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  @include cover; }

img {
  max-width: 100%; }

// .is-animated
//   &[data-loaded="true"]
//     animation: fadeIn .4s

.overflow-hidden {
  overflow: hidden;
  padding-right: 1px; }

.icon {
  display: inline-block; }

.logo {
  @include flex(center, center);
  width: 182px;
  height: $header-height;
  @include xs {
    width: 70px;
    height: auto; }
  &.is-red {
    background-color: #b80a00; } }

.copy {
  font-size: 14px;
  text-transform: uppercase;
  color: rgba($white, .5);
  @include thin; }

.scroll-down {
  text-align: center;
  &__title {
    text-transform: uppercase;
    color: rgba($white, .5);
    font-size: 14px;
    @include thin; }
  &__btn {
    display: inline-block; }
  &__mouse {
    display: block;
    position: relative;
    border: 2px solid rgba($white, .5);
    height: 45px;
    width: 26px;
    border-radius: 25px;
    @include before {
      width: 5px;
      height: 5px;
      display: inline-block;
      background-color: rgba($white, .5);
      border-radius: 50%;
      position: absolute;
      top: 8px;
      left: 50%;
      transform: translate(-50%, 0); } }
  &__arr {
    display: inline-block;
    position: relative;
    height: 25px;
    width: 26px;
    margin-bottom: 15px;
    @include before {
      display: inline-block;
      @include icon-arrow(15, 15, 2, rgba($white, .5), -45);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0) rotate(-45deg);
      animation-name: scrollDown;
      animation-duration: 1s;
      animation-iteration-count: infinite; }
    @include after {
      @extend .scroll-down__arr::before;
      top: 10px;
      animation-duration: 1s;
      animation-delay: .1s;
      animation-iteration-count: infinite; } } }

.btn {
  display: inline-block;
  background-color: transparent;
  text-align: center;
  text-transform: uppercase;
  color: $white;
  border: 1px solid  $white;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 30px;
  padding-right: 30px;
  @include tr(background-color .3s, border-color .3s);
  @include hover {
    background-color: $red;
    border-color: $red; } }

.line-sm {
  height: 45px;
  width: 1px;
  background-color: $white;
  display: inline-block; }

.btn-link {
  font-size: 12px;
  text-decoration: underline;
  color: $white;
  @include light;
  text-transform: uppercase;
  @include hover {
    text-decoration: none; } }

@keyframes scrollDown {
  0% {
    opacity: 0.3; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0.3; } }

